import './AdminClienteUpdate.css'

import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import moment from 'moment'
import React, { useEffect, useState } from 'react'
import { RouteComponentProps } from 'react-router'

import { AdminItemField, Page } from '../../components'
import { RUTAS } from '../../constants'
import {
  DetailViewProps,
  format_numero_telefono_input,
  format_numero_telefono_verificar,
  useTryGoBack
} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import {DATE_FORMAT} from "../../utils/date";

const defaultValues = {
  id: '',
  first_name: '',
  last_name: '',
  email: '',
  username: '',
  nro_telefono: '',
  nro_documento: '',
  razon_social: '',
  ruc: '',
  contrasena: '',
  repetir_contrasena: '',
  imagen_perfil: null,
  fecha_nacimiento: '',
}

export const AdminClienteUpdate: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { adminStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [Errors, setErrors] = useState(defaultValues)
  const [Datos, setDatos] = useState(defaultValues as any)
  const tryGoBack = useTryGoBack()
  const idCliente = match.params.id

  const setup = async () => {
    if(idCliente) {
      obtenerCliente(idCliente)
    } else {
      setDatos(defaultValues)
    }
  }

  const obtenerCliente = (cliente_id: number) => {
    // @ts-ignore
    rootStore.environment.api.getCliente(cliente_id).then(result => {
      if (result.kind === "ok") {
        console.log(result)
        let datos = result.cliente
        datos.fecha_nacimiento = datos.fecha_nacimiento ? moment(datos.fecha_nacimiento).format('DD/MM/YYYY') : ''
        datos.nro_telefono = format_numero_telefono_input(datos.nro_telefono)
        setDatos(datos)
      }
    })
  }

  useEffect(() => {
    setup()
  }, [idCliente])

  const setDato = (campo: string, valor: string) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const guardar = () => {
    setLoading(true)
    setErrors(defaultValues)
    const datos = { ...Datos, username: Datos.email }
    datos.nro_telefono = format_numero_telefono_verificar(datos.nro_telefono)
    if (datos.fecha_nacimiento && moment(datos.fecha_nacimiento, DATE_FORMAT.DATE).isValid()) {
      datos.fecha_nacimiento = moment(datos.fecha_nacimiento, DATE_FORMAT.DATE).format("YYYY-MM-DD")
    } else {
      delete datos.fecha_nacimiento
    }
    rootStore.environment.api.guardarCliente(datos).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        tryGoBack(RUTAS.ADMIN_CLIENTE)
      } else if (result.kind === 'bad-data') {
        setErrors(result.errors)
        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
      } else {
        alert('Ocurrió un error al guardar vuelva a intentar en unos momentos')
      }
    })
  }
  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }
  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">{Datos.id ? 'Editar cliente' : 'Crear cliente'}</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item">
                  <a href="/admin/cliente">Clientes</a>
                </li>
                <li className="breadcrumb-item active">{Datos.id ? 'Editar' : 'Crear'}</li>
              </ol>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-body">
                  <div className="row">
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'Nombre'}
                      tipo={'texto'}
                      campo={'first_name'}
                      valor={Datos ? Datos.first_name : ''}
                      error={Errors && Errors.first_name}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'Apellido'}
                      tipo={'texto'}
                      campo={'last_name'}
                      valor={Datos ? Datos.last_name : ''}
                      error={Errors && Errors.last_name}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'Correo electrónico'}
                      tipo={'email'}
                      campo={'email'}
                      valor={Datos ? Datos.email : ''}
                      error={Errors && Errors.email}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'Número de celular'}
                      tipo={'telefono-simple'}
                      campo={'nro_telefono'}
                      valor={Datos ? Datos.nro_telefono : ''}
                      error={Errors && Errors.nro_telefono}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'Número de cédula'}
                      tipo={'texto'}
                      campo={'nro_documento'}
                      valor={Datos ? Datos.nro_documento : ''}
                      error={Errors && Errors.nro_documento}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'Fecha de nacimiento'}
                      tipo={'fecha'}
                      campo={'fecha_nacimiento'}
                      valor={Datos ? Datos.fecha_nacimiento : ''}
                      error={Errors && Errors.fecha_nacimiento}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'Razón social'}
                      tipo={'texto'}
                      campo={'razon_social'}
                      valor={Datos ? Datos.razon_social : ''}
                      error={Errors && Errors.razon_social}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'RUC'}
                      tipo={'texto'}
                      campo={'ruc'}
                      valor={Datos ? Datos.ruc : ''}
                      error={Errors && Errors.ruc}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                  </div>
                  <div className="row">
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'Contraseña'}
                      tipo={'texto'}
                      campo={'password'}
                      valor={Datos ? Datos.contrasena : ''}
                      error={Errors && Errors.contrasena}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                    <AdminItemField
                      deshabilitado={!Loading}
                      titulo={'Repetir contraseña'}
                      tipo={'texto'}
                      campo={'repetir_contrasena'}
                      valor={Datos ? Datos.repetir_contrasena : ''}
                      error={Errors && Errors.repetir_contrasena}
                      set_dato={setDatoInput}
                      columnas={12}
                      columnas_sm={3}
                    />
                  </div>
                  <div className={'form-group text-right'}>
                    <button className={'btn btn-light'} onClick={() => history.push('/admin/cliente')}>
                      Cancelar
                    </button>
                    <button className={'btn btn-primary ml-2'} disabled={Loading} onClick={guardar}>
                      {Loading && <IonSpinner name="crescent" />}
                      {!Loading && <span>Guardar</span>}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminClienteUpdate
